body {
	font-family: $font-family;
	line-height: 1.4;
}

h1,
h2,
h3 {
	line-height: 1.2;
}

.container {
	max-width: $max-width;
	margin-left: auto;
	margin-right: auto;
	padding-left: $gutter;
	padding-right: $gutter;
}

a {
	transition: 0.15s;
}

.icon {
	display: inline-block;
	width: 24px;
	height: 24px;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor;
}

.select select,
.button,
.text {
	font-family: inherit;
	border-radius: $border-radius;
	border: 0;
	font-weight: bold;
	background-color: #fff;
}

.button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding-left: 20px;
	padding-right: 20px;
	text-align: center;
	height: $finger-size;
	color: white;
	background-color: $color-blue;

	&-icon {
		justify-content: center;
		color: black;
		padding-left: 0;
		padding-right: 0;
		width: $finger-size;
		background-color: transparent;
	}

	&-full {
		width: 100%;
	}

	&-navy {
		background-color: $color-darkblue;
	}
}

.text {
	min-height: $finger-size;
	border-radius: $border-radius;
	color: $color-darkblue;
	padding-left: 10px;
	padding-right: 10px;
	width: 100%;

	&::placeholder {
		color: rgba($color-darkblue, 0.5);
	}
}

.select {
	position: relative;
	color: $color-darkblue;

	select {
		-webkit-appearance: none;
		display: inline-block;
		width: 100%;
		padding-right: 3 0px;
		color: $color-darkblue;
		height: $finger-size;
	}

	&-icon {
		pointer-events: none;
		z-index: 1;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
	}
}

.form-item {
	position: relative;

	.form-item-icon {
		pointer-events: none;
		z-index: 1;
		position: absolute;
		left: 10px;
		top: 50%;
		transform: translateY(-50%);
		color: $color-darkblue;
	}
	.text {
		padding-left: 40px;
	}

	.select select {
		padding-left: 40px;
	}
}

.form-description {
	margin-top: 5px;
	font-size: $font-size-small;
	@include mq('tablet') {
		font-size: $font-size;
		margin-top: 20px;
	}
}

.form-checkbox {
	display: flex;
	align-items: center;
}

.checkbox {
	position: relative;
	display: flex;

	input {
		position: absolute;
		opacity: 0;
		top: 0;
		left: 0;
		pointer-events: none;
	}

	&:before {
		content: '';
		display: inline-block;
		font-size: 18px;
		width: 1em;
		height: 1em;
		border: 2px solid white;
	}

	.icon {
		display: none;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		margin-left: 3px;
		margin-top: -2px;
		width: 20px;
		height: 20px;
	}

	input:checked + .icon {
		display: block;
	}
}
